.chat{
  &__wrapper{
    background: #0A2545;
    border-radius: 16px;

    padding: 26px;
  }
  &-wrapper{
    &__msg + &__msg{
      margin-top: 40px;
    }
    &__content{
      height: 370px;
      overflow-y: auto;
      overflow-x: hidden;

      padding-bottom: 30px;
    }
    &__footer{
      display: flex;
      align-items: center;

      background: #002D58;
      border-radius: 10px;

      padding: 18px 20px;
    }

  }
  &-msg{
    &__wrapper{
      display: flex;
      align-items: flex-start;
    }
    &__img + &__content{
      margin-left: 20px;
    }
    &__img{
      width: 38px;
      height: 38px;

      border-radius: 50%;

      overflow: hidden;

      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__text{
      border: 2px solid #1E5BA4;

      padding: 18px 15px;

      letter-spacing: -0.3px;

      color: #FFFFFF;
      font-size: 18px;
      line-height: 1.3;

      &:nth-child(odd){
        border-radius: 10px 10px 10px 0px;
      }
      &:nth-child(even){
        border-radius: 0px 10px 10px 10px;
      }

      &_me{
        background: #1E5BA4;
      }
      &:nth-child(odd){
        border-radius: 10px 10px 0px 10px;
      }
      &:nth-child(even){
        border-radius: 10px 0px 10px 10px;
      }
    }

    &__text + &__text{
      margin-top: 15px;
    }

    &__content{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      &_me{
        justify-content: flex-end;
        align-items: flex-end;
      }
    }

    &__time{
      letter-spacing: -0.3px;

      color: #1E5BA4;

      font-size: 14px;

      margin-top: 10px;
    }
  }
}

.send-msg{
  width: 100%;
  &__wrapper{
    display: flex;
    align-items: center;
  }
  &__attach{
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: 100%;
    }
  }
  &__input{
    width: 100%;
    padding: 0 18px;
    input{
      width: 100%;

      border: none;
      outline: none;
      background: none;

      font-size: 18px;
      letter-spacing: -0.3px;
      color:#fff;
      &::placeholder{
        opacity: 1;
        color: #1E5BA4;
      }
    }
  }
  &__btn{
    flex-shrink: 0;
  }
}