.offers{
  &-item + &-item{
    margin-top: 40px;
  }
  &-result{
    &__grid{
      margin-top: 40px;

      display: grid;
      grid-template-columns: repeat(3,minmax(0,1fr));
      gap: 30px;
    }

    &__btn{
      max-width: 490px;
      width: 100%;
      margin: 45px auto 0 auto;
    }
  }
  &-sort{
    display: flex;
    align-items: center;
    &__result{
      margin-left: 25px;
      &-text{
        font-size: 18px;
        line-height: 21px;
        letter-spacing: -0.3px;
        color: #297BC7;

        span{
          color:#fff;
        }
      }
    }
  }
  &__desc{
    margin-top: 30px;
    &-item{
      display: flex;
      align-items: center;
    }
    &-item + &-item{
      margin-top: 14px;
    }
    &-title{
      color: #297BC7;
      font-size: 18px;
    }
    &-text{
      color: #FFFFFF;
      font-size: 18px;
      font-weight: 500;
      margin-left: auto;
    }
  }
  &__input{
    width: 100%;

    height: 60px;

    background: #0A2545;
    border-radius: 14px;

    display: flex;
    justify-content: space-between;
    padding-right: 15px;
    input{
      font-weight: 500;
      font-size: 18px;
      color:#fff;

      width: 100%;
      height: 100%;

      border: none;
      outline: none;
      background: none;

      padding: 20px;

      &::placeholder{
        opacity: 1;
        letter-spacing: -0.3px;
        color: #14386E;
      }
    }
  }
  &__select{
    font-size: 18px;
    font-weight: 500;
    color:#ffff;
    background: none;
    outline: none;
    border: none;
  }

  &__wrapper{
    max-width: 530px;
    width: 100%;

    margin: 0 auto;
  }

  &__footer{
    margin-top: 18px;
  }


  &__arrow{
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 15px 0;
  }

  &__content{
    margin-top: 40px;
  }

  &__exchange{
    text-align: center;

    color: #297BC7;

    font-size: 22px;
  }

  &__btn{
    width: 100%;
    height: 60px;
  }
}

a.offer-item__wrapper{
  &:hover{
    border-color:#fff;

    .offer-item__header{
      border-color: #fff;
    }
  }
}

.offer{
  &-item{
    &__wrapper{
      display: flex;
      flex-direction: column;

      border: 2px solid #1E5BA4;
      border-radius: 16px;
      overflow:hidden;

      height: 100%;

    }
    &__header{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      padding: 25px 30px;

      border-bottom: 1px solid #1E5BA4;
    }
    &__account{
      display: flex;
      align-items: stretch;
    }
    &__payment{
      display: flex;
      align-items: center;
    }
    &__info{
      padding: 25px;
      &-block + &-block{
        margin-top: 15px;
      }
      &-item{
        display: flex;
        align-items: center;
      }
      &-item + &-item{
        margin-left: 30px;
      }
      &-label{
        display: flex;
        align-items: center;
      }
      &-label + &-label{
        margin-left: 10px;
      }
      &-content{
        display: flex;
        align-items: center;
        margin-left: 10px;
      }
      &-icon{
        width: 20px;
        height: 20px;
        flex-shrink: 0;

        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-title{
        font-size: 16px;
        letter-spacing: -.3px;
        color:rgba(41, 123, 199, 1);
      }
      &-text{
        margin-left: 10px;

        font-weight: 500;
        font-size: 16px;
        color:#fff;
      }
    }
    &__footer{
      background: #0A2545;

      padding: 25px;
    }
    &__price{
      display: flex;
      align-items: center;
      &-label{
        font-weight: 500;
        font-size: 34px;
        letter-spacing: -0.3px;
        color: #FFFFFF;
      }
      &-trend{
        margin-left: 20px;
      }
      &-text + &-text{
        margin-left: 30px;
      }
      &-text{
        font-size: 16px;
        letter-spacing: -0.3px;

        color: #297BC7;
      }
    }
    &__price + &__price{
      margin-top: 12px;
    }
  }
  &-account{
    &__img{
      width: 60px;
      height: 60px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__name{
      font-size: 22px;
      font-weight: 500;
      letter-spacing: -0.3px;
      color: #FFFFFF;

      &_vip{
        display: flex;
        align-items: center;

        &::after{
          content: '';
          width: 20px;
          height: 20px;
          margin-left: 20px;
          display: block;

          background: url("../img/logo-offer.svg") center center no-repeat;
          background-size: contain;
        }
      }
    }
    &__info{
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__stats{
      display: flex;
      align-items: center;
      &-item{
        display: flex;
        align-items: center;
      }
      &-item + &-item{
        margin-left: 15px;
      }
      &-icon{
        flex-shrink: 0;

        width: 14px;
        height: 14px;

        display: flex;
        align-items: center;
        justify-content: center;
        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-label{
        margin-left: 8px;

        font-size: 16px;
        letter-spacing: -0.3px;
        color: #297BC7;
      }
    }
  }
  &-info{
    height: 100%;
    &__wrapper{
      height: 100%;
      display: flex;
      flex-direction: column;

      background: #14386E;
      border-radius: 16px;

      padding: 25px;
    }
    &__content{
      margin-top: auto;
      padding-top: 15px;
    }
    &__title{
      font-size: 30px;
      font-weight: 500;
      color:#fff;
    }
    &__desc{
      margin-top: 15px;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.3px;
      color:#FFf;
    }
    &__btn{
      margin-top: 35px;
    }
  }
  &-term{
    &__wrapper{
      padding-top: 45px;
    }
    &__title{
      font-size: 22px;
      font-weight: 500;

      color:#fff;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 30px;

      margin-top: 20px;
    }
    &__text + &__text{
      margin-top: 15px;
    }
    &__text{
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.3px;

      color: #297BC7;
    }
  }
}

.calc{
  height: 100%;
  &-input + &-input{
    margin-top: 20px;
  }
  &-input{
    &__label{
      font-size: 16px;
      letter-spacing: -0.1px;
      color:#fff;
    }
    &__content{
      margin-top: 20px;
    }
  }
  &__wrapper{
    padding: 25px;

    display: flex;
    flex-direction: column;

    height: 100%;
  }
  &__title{
    font-size: 30px;
    font-weight: 500;
    color:#fff;
  }
  &__desc{
    margin-top: 15px;
    font-size: 18px;
    color:#297BC7;

    letter-spacing: -0.3px;
  }
  &__content{
    margin-top: auto;
    padding-top: 15px;
  }
  &__input{
    width: 100%;

    height: 60px;

    background: #0A2545;
    border-radius: 14px;

    display: flex;
    justify-content: space-between;
    padding-right: 15px;
    input{
      font-weight: 500;
      font-size: 18px;
      color:#fff;

      width: 100%;
      height: 100%;

      border: none;
      outline: none;
      background: none;

      padding: 20px;

      &::placeholder{
        opacity: 1;
        letter-spacing: -0.3px;
        color: #14386E;
      }
    }

    &-text{
      font-size: 18px;
      color:#fff;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

@media screen and (max-width: 1440px) {
  .offers{
    &-result{
      &__grid{
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
  }
  .offer{
    &-item{
      &__payment{
        align-items: flex-start;
        flex-direction: column;
      }
      &__info{
        &-item + &-item{
          margin-left: 0;
          margin-top: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .offers{
    &-result{
      &__grid{
        grid-template-columns: minmax(0,1fr);
      }
    }
  }
  .offer{
    &-term{
      &__grid{
        grid-template-columns: minmax(0,1fr);
      }
    }
  }
}