.deal{
  &-timer{
    display: flex;
    align-items: center;
  }
  &-controls{
    display: flex;
    align-items: center;
    &__item + &__item{
      margin-left: 30px;
    }
  }
  &-grid{
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    gap: 30px;
    &__item{
      &_big{
        grid-column-end: span 2;
      }
    }
  }

  &-block{
    height: 100%;
    &__wrapper{
      height: 100%;

      padding: 18px 25px;

      border: 2px solid #1E5BA4;
      border-radius: 16px;
    }
    &__content{
      padding: 12px 0;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 15px 30px;
    }
    &__title{
      font-weight: 500;
      font-size: 22px;
      letter-spacing: -0.3px;

      color: #FFFFFF;
      display: flex;
      align-items: center;
      a{
        font-weight: 500;
        font-size: 22px;
        letter-spacing: -0.3px;

        color: #329DFF;
        margin-left: 12px;
      }
    }
    &__title + &__grid{
      margin-top: 20px;
    }
    &__alert{
      letter-spacing: -0.3px;

      color: #FF3333;

      font-size: 16px;

      margin-top: 15px;
    }
    &__text{
      font-size: 18px;
      letter-spacing: -0.3px;

      color: #297BC7;
      span{
        color:#fff;
      }
    }
  }

  &-desc{
    display: flex;
    align-items: center;
    &__text{
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.3px;

      color: #297BC7;
    }
    &__img{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;

      flex-shrink: 0;

      margin-left: 80px;

      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  &-payment{
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__title{
      font-weight: 500;
      font-size: 18px;
      color:#fff;
    }
    &__list{
      display: flex;
      align-items: center;
      &-item + &-item {
        margin-left: 5px;
      }
    }
  }

  &-offer{
    height: 100%;
    &__wrapper{
      height: 100%;
    }
    &__header{
      padding: 0!important;
    }
    &__account{
      padding: 30px 25px;
    }
    &__footer{
      margin-top: auto;
    }
    &__status{
      padding: 13px;
      max-width: 100px;
      height: 100%;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;

      background-color: rgba(10, 37, 69, 1);
      border-left: 1px solid #1E5BA4;
      &-text{
        text-transform: uppercase;
        font-size: 22px;
        letter-spacing: -0.3px;

        color: #FFFFFF;
      }
    }
    &-grid{
      gap: 20px 30px;
    }
  }

  &-paid{
    background: #66FF66;
    &__title{
      font-weight: 500;
      font-size: 22px;
      text-transform: uppercase;
      letter-spacing: -0.3px;

      color: #1E5BA4;
    }
  }

  &__wrapper{
    max-width: 1010px;
    width: 100%;
    margin: 0 auto;
  }

  &__chat{
    margin-top: 30px;
  }

  &__timer{
    display: flex;
    align-items: center;
    margin-left: 30px;
    &-item{
      font-size: 44px;
      font-weight: 400;
      color:#297BC7;
      &::after{
        content: ':';
      }
      &:last-child{
        &::after{
          display: none;
        }
      }
    }
  }
}

.copy-link{
  display: flex;
  align-items: center;
  &::after{
    content: '';
    width: 20px;
    height: 20px;
    display: block;
    margin-left: 10px;

    background: url("../img/copy.svg") center center no-repeat;
    background-size: contain;
  }
}

.payment{
  &-block{
    &__wrapper{
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }
  &-radio{
    &__input{
      display: none;
      &:checked ~ .payment-radio__checkmark{
        background: #1E5BA4;
        border-color:#FFFFFF;
      }
    }
    &__checkmark{
      width: 66px;
      height: 66px;

      display: flex;
      align-items: center;
      justify-content: center;

      border-radius: 16px;
      border:2px solid transparent;

      transition: .3s all;
      img{
        width: 40px;
        height: 40px;
        object-fit: contain;
      }
    }
  }
}

.deal{
  &-close{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 40px;
    }
    &__name{
      font-weight: 500;
      font-size: 22px;

      letter-spacing: -0.3px;
      color:#fff;
    }
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__radio + &__radio{
      margin-left: 15px;
    }
    &__textarea{
      width: 100%;
      height: 275px;

      background: #0A2545;
      border-radius: 16px;

      textarea{
        width: 100%;
        height: 100%;
        padding: 25px 20px;
        resize: none;
        background: none;
        border: none;
        outline: none;
        color:#fff;
      }
    }
    &__radio{
      &-input{
        display: none;
        &:checked ~ .deal-close__radio-checkmark_like{
          path{
            fill: #66FF66;
          }
        }
        &:checked ~ .deal-close__radio-checkmark_dislike{
          path{
            fill: #FF3333;
          }
        }
      }
      &-checkmark{
        path{
          fill: #297BC7;
        }
      }
    }
  }
}

@media screen and (min-width: 769px) {
  .deal{
    &-grid{
      &__item{
        &_row{
          &-big{
            grid-row-end: span 4;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .deal{
    &-grid{
      grid-template-columns: 1fr;
      &__item{
        &_big{
          grid-column-end: span 1;
        }
      }
    }
    &-desc{
      &__img{
        margin-left: 20px;
      }
    }
    &-timer{
      flex-direction: column;
    }
    &-payment{
      &__wrapper{
        flex-direction: column;
      }
      &__list{
        margin-top: 15px;
        flex-wrap: wrap;
        justify-content: center;
      }
    }
    &-block{
      &__wrapper{
        padding-top: 15px;
        padding-bottom: 15px;
      }
    }
    &__timer{
      margin-left: 0;
      margin-top: 15px;
    }
    &__header{
      flex-direction: column;
    }
    &__controls{
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 450px) {
  .deal{
    &-offer{
      &__header{
        flex-direction: column;
      }
      &__status{
        width: 100%;
        max-width: 100%;
      }
    }
  }
}