.left-aside{

  background-color: #14386E;

  flex-shrink: 0;

  width: 320px;
  &__wrapper{
    padding: 60px 0;
  }
}

.page-list{

  transition:.3s all;
  &.hide + .page-content{
    width: 100%;
  }
  &__item{
    padding: 0 34px;

    &_current{
      .page-list__link{
        &-icon{
          path{
            fill: #fff;
          }
        }
        &-text{
          color:#fff;
        }
      }
    }
  }
  &__item + &__item{
    margin-top: 40px;
  }
  &__link{
    &-wrapper{
      display: flex;
      align-items: center;
      position: relative;
      &::after{
        content: '';
        width: 16px;
        height: 16px;
        display: block;

        background: url("../img/white-arrow.svg") center center no-repeat;
        background-size: contain;

        margin-left: auto;
      }
    }
    &-icon{
      display: flex;
      align-items: center;
      justify-content: center;

      flex-shrink: 0;

      width: 20px;
      height: 20px;

      svg{
        width: 100%;
        height: 100%;
      }
    }
    &-text{
      margin-left: 17px;

      font-weight: 500;
      font-size: 18px;
      color: #329DFF;
    }
  }
}

.aside{
  &-languages{
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 30px;

    .language-select{
      color:#329DFF;
    }
    &__label{
      font-size: 18px;
      color:#fff;

    }
  }
  &-profile{
    position: relative;
    &__settings{
      position: absolute;
      width: 20px;
      height: 20px;
      right: 0;
      top: 50%;
      margin-top: -10px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__links{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__link{
      color: #329DFF;
      margin-top: 10px;
    }
    &__img{
      &-item{
        width: 60px;
        height: 60px;
      }
      &-verify{
        width: 20px;
        height: 20px;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }
  }
}

@media screen and (min-width: 992px) {
  .left-aside{
    &.hide{
      width: 80px;
      transition: .3s all;
    }
    &.hide{
      .page-list{
        .page-list{
          &__item{
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          &__link{
            &-text,&-wrapper::after{
              display: none;
            }
          }
        }
      }
    }
    &__block{
      &_first,&_third{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 991px) {

  body{
    &.no-scroll-mob{
      overflow: hidden;
      position: relative;
    }
  }

  .left-aside{
    position: fixed;
    left: 0;
    top: 70px;
    height: 100vh;
    z-index: 99;
    transform: translateX(-100%);
    &.hide{
      transform: translateX(0);
    }
    &__block{
      padding: 30px;
    }
    &__block + &__block{
      border-top: 1px solid #1E5BA4;
    }
    &__wrapper{
      padding: 0;
    }
  }
  .page-list{
    &__item{
      padding: 0;
    }
  }
}