.settings{
  &-grid{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    &__item + &__item{
      margin-left: 30px;
    }
    &__item{
      max-width: 490px;
      width: 100%;
      flex-shrink: 0;
    }
  }
  &-tabs{
    &__content{
      margin-top: 40px;
    }
  }
  &-content{
    &__header{
      margin-bottom: 40px;
    }
  }
  &-wrapper{
    &__item{
      padding: 50px 30px 66px 30px;

      border: 2px solid #1E5BA4;
      border-radius: 16px;
    }

    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-label{
        font-size: 30px;
        font-weight: 500;

        line-height: 1.16;

        color:#fff;
      }
    }

    &__chat{
      margin-top: 40px;
    }

    &__item + &__item{
      margin-top: 30px;
    }

    &__content{
      max-width: 1010px;
      width: 100%;
      margin: 0 auto;
    }

    &__btn{
      height: 60px;
      margin-top: 60px;
    }
  }
  &-list{
    display: flex;
    align-items: center;
    &__item + &__item{
      margin-left: 40px;
    }
    &__item{
      display: flex;
      align-items: center;
      &_current{
        .settings-list__link{
          color: #fff;
        }
      }
    }
    &__link{
      font-weight: 500;
      font-size: 22px;
      letter-spacing: -0.3px;

      color: #329DFF;
      & + .notification-counter{
        margin-left: 12px;
      }
    }
  }
  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-btn{
      width: 170px;
      font-size: 18px;
    }
  }
}

.new-link{
  &__label{
    font-size: 18px;
    letter-spacing: -0.3px;

    color: #329DFF;
  }
  &__wrapper{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 18px;
    height: 18px;
    display: block;
    margin-left: 15px;

    svg{
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 475px) {
  .settings{
    &-list{
      &__item + &__item{
        margin-left: 20px;
      }
      &__link{
        font-size: 18px;
      }
    }
  }
}