.blank{
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap: 30px;
  }
  &-item{
    &__wrapper{
      border: 2px solid #1E5BA4;
      border-radius: 16px;
    }
    &__header{
      padding: 25px;
      border-bottom: 1px solid #1E5BA4;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__logo{
      display: flex;
      align-items: center;
      &-img{
        width: 40px;
        height: 40px;
        flex-shrink: 0;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      &-label{
        margin-left: 15px;

        font-size: 22px;
        font-weight: 500;
        color:#FFF;
      }
    }
    &__controls{
      display: flex;
      align-items: center;
      &-item + &-item{
        margin-left: 20px;
      }
      &-item{
        width: 20px;
        height: 20px;
        flex-shrink: 0;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
    &__content{
      padding: 20px 25px 40px 25px;
    }
  }
  &-desc{
    &__title{
      font-size: 16px;
      letter-spacing: -.3px;
      color:#fff;
      margin-bottom: 10px;
    }
    &__text{
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -.3px;

      color: #297BC7;
    }
    &__text + &__text{
      margin-top: 10px;
    }
  }
}

.edit-blank{
  &__header{
    background: #002D58;
    border-radius: 14px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    padding: 15px;
  }
  &__content{
    background: #002D58;
    border-radius: 14px;

    padding: 15px;
    margin-top: 20px;
  }

  &__btn{
    margin-top: 30px;
  }
}

@media screen and (max-width: 1440px) {
  .blank{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
    }
  }
}

@media screen and (max-width: 767px) {
  .blank{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
  }
}