.header {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    background: #14386E;
    filter: drop-shadow(0px 4px 12px rgba(0, 0, 0, 0.15));

    padding: 0 48px 0 33px;
    height: 70px;
  }

  &__block {
    display: flex;
    align-items: center;
  }

  &__menu {
    margin-right: 30px;
  }

  &__notification {
    margin-left: 30px;
  }

  &__profile {
    margin-left: 45px;
  }

  &__languages {
    margin-left: 40px;
  }

  &-notification {
    &__counter {
      position: absolute;
      top: -8px;
      right: -12px;
    }
  }
}

.burger {
  width: 22px;
  height: 22px;

  transition: .3s all linear;

  svg {
    width: 100%;
    height: 100%;
  }

  &.hide {
    transform: scale(-1, 1) translateX(5px);
  }
}

.notification, .refresh {
  width: 22px;
  height: 22px;

  svg {
    width: 100%;
    height: 100%;
  }
}

.notification {
  position: relative;

  &-counter {

    display: flex;
    align-items: center;
    justify-content: center;

    width: 16px;
    height: 16px;
    border-radius: 50%;

    background-color: #FF9933;

    font-size: 12px;
    color: #fff;
    font-weight: 700;
  }
}

.profile {
  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__link {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: -0.3px;
    color: #FFFFFF;
  }

  &__link + &__img{
    margin-left: 14px;
  }

  &__img + &__link{
    margin-left: 14px;
  }

  &__img {
    position: relative;

    &-item {
      width: 34px;
      height: 34px;
      border-radius: 50%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-verify {
      position: absolute;
      top: -3px;
      right: -7px;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__profile, &__notification, &__languages {
      display: none;
    }

    &__block {
      &_logo {
        width: 100%;
      }
    }
    &__logo{
      margin: 0 auto;
    }
    &__menu{
      margin-right: 0;
    }
  }
}
