.profile{
  &-wrapper{
    &__block + &__block{
      margin-top: 45px;
    }
  }
  &-block{
    &__title{
      display: flex;
      align-items: center;
      justify-content: space-between;

      margin-bottom: 40px;
    }
    &__label{
      font-size: 22px;
      font-weight: 500;
      letter-spacing: -0.3px;

      color:#fff;
    }
  }
  &-item{
    &__input + &__input{
      margin-top: 12px;
    }
    &__input_radio + &__input_radio{
      margin-top: 20px;
    }
    &__input{
      &-text{
        font-size: 16px;
        letter-spacing: -0.3px;

        color: #297BC7;
      }
      &-item{
        margin-top: 12px;
        width: 100%;

        height: 60px;

        background: #0A2545;
        border-radius: 14px;

        input{
          font-weight: 500;
          font-size: 18px;
          color:#fff;

          width: 100%;
          height: 100%;

          border: none;
          outline: none;
          background: none;

          padding: 20px;
          &::placeholder{
            opacity: 1;
            letter-spacing: -0.3px;
            color: #14386E;
          }
        }
      }
    }
  }
  &__wrapper{
    position: relative;
  }
  &__list{
    position: absolute;
    top: 60px;
    left:0;

    background: #1E5BA4;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;

    padding: 18px 40px 18px 16px;
  }
  &-list{
    &__link{
      color:#fff;
      font-size: 18px;
      letter-spacing: -0.03px;
      &:hover{
        color: #329DFF;
      }
    }
    &__item + &__item{
      margin-top: 12px;
    }
  }
}

.verify{
  display: flex;
  align-items: center;
  &__text{
    font-size: 18px;
    letter-spacing: -0.408px;
    color: #329DFF;
  }
  &__icon{
    margin-left: 12px;
  }
  &_verified{
    .verify__text{
      color: #66FF66;
    }
    .verify__icon{
      path{
        fill-opacity: 1;
        fill:#66FF66;
      }
    }
  }
}


@media screen and (max-width: 1440px) {
  .settings{
    &-grid{
      flex-direction: column;
      &__item{
        width: 100%;
        max-width: 100%;
      }
      &__item + &__item{
        margin-left:0;
        margin-top: 30px;
      }
    }
  }
}
