.notification{
  &-tabs{
    &__content{
      margin-top: 40px;
    }
  }
  &-block{
    &__item + &__item{
      margin-top: 20px;
    }
  }
  &-item{
    &_not-read{
      .notification{
        &-item{
          &__title{
            color: #FF9933;
          }
        }
      }
    }
    &_chat{
      .notification{
        &-item{
          &__title{
            &::after{
              content: '';
              flex-shrink: 0;
              width: 15px;
              height: 15px;
              display: block;

              background: url("../img/white-arrow.svg") center center no-repeat;
              background-size: contain;
            }
          }
        }
      }
    }
    &__wrapper{
      background: #002D58;
      border-radius: 14px;

      padding: 15px;

      &_dark{
        background-color: #0A2545;
      }
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__date{
      font-size: 16px;
      letter-spacing: -0.3px;

      color: #1E5BA4;
    }
    &__title{
      font-size: 18px;
      font-weight: 500;
      color: #FFFFFF;
      margin-top: 10px;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__text{
      font-size: 16px;
      line-height: 22px;
      color: #297BC7;
      margin-top: 5px;

      max-width: 400px;
      width: 100%;
    }
  }
}