.table{
  .swiper-slide{
    min-width: 1500px;
    width: 100%;
  }
  &-row{
    display: grid;
    width: 100%;
    &_header{
      background: #0A2545;
      border-radius: 14px 14px 0px 0px;
      padding: 20px;
    }
    &_content{
      padding: 20px;
      & + &{
        border-top: 1px solid #1E5BA4;;
      }
      &.table-partners__row,&.table-deals__row{
        padding: 12px 20px;
      }
    }
  }
  &-offer{
    &__row{
      grid-template-columns: 30px auto auto auto 250px 200px 85px 20px;
      gap: 70px;
    }
  }
  &-partners{
    &__row{
      grid-template-columns: 150px auto 110px 155px 80px 100px;
      gap: 100px;
    }
  }
  &-deals{
    &__row{
      grid-template-columns: auto 250px 110px 155px 100px 50px;
      gap: 50px;
    }
  }
  &-payment{
    display: flex;
    align-items: center;
    &__item + &__item{
      margin-left: 10px;
    }
    &__img{
      width: 24px;
      height: 24px;
      object-fit: contain;
    }
  }
  &__column{
    display: flex;
    align-items: center;
  }
  &__wrapper{
    border-radius: 16px;
    border: 2px solid #1E5BA4;
  }
  &__title{
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    color: #297BC7;
  }
  &__text{
    font-size: 16px;
    line-height: 22px;
    color:#fff;

    display: flex;
    align-items: center;
    &-label{
      display: block;

      span{
        font-weight: 500;
      }
    }
  }
  &__text + &__text{
    margin-left: 20px;
  }
  &__icon{
    flex-shrink: 0;
    img{
      width: 36px;
      height: 36px;
      border-radius: 50%;
    }
    svg{
      width: 18px;
      height: 18px;
      display: block;
    }
  }
  &__text-label + &__icon{
    margin-left: 12px;
  }
  &__icon + &__text-label{
    margin-left: 12px;
  }
  &__link{
    font-size: 18px;
    letter-spacing: -0.3px;
    color: #329DFF;
  }
  &__btn{
    height: 38px;

    font-size: 16px;
  }
}

.status{
  &_succes{
    color:#66FF66;
  }
  &_failed{
    color:#FF3333;
  }
  &_neutral{
    color:rgba(30, 91, 164, 1);
  }
}