*{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;

  font-family: 'Roboto',sans-serif;
}

.btn{
  -webkit-appearance: none;
  outline: none;
  border: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 64px;

  font-size: 24px;
  font-weight: 700;
  letter-spacing: -0.3px;

  color: #FFFFFF;

  &:hover{
    cursor: pointer;
  }

  &-yellow{
    background: linear-gradient(111.04deg, #FFD900 -19.61%, #E86F00 94.09%);
    box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
  &-blue{
    background: linear-gradient(111.04deg, #32C5FF -19.61%, #1E5BA4 94.09%);
    box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
  &-disabled{
    background: #C4C4C4;

    border-radius: 10px;

    pointer-events: none;
  }
  &-red{
    background: linear-gradient(111.04deg, #FF3333 -19.61%, #A51623 94.09%);
    box-shadow: 0px 32px 20px -20px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
  }
}


.page-wrapper{
  &__content{
    display: flex;
    align-items: stretch;
    min-height: calc(100vh - 70px);
  }
}

.page-content{
  width: 100%;
  &__wrapper{
    background-color:#002D58;
    width: 100%;
    height: 100%;

    padding: 48px 33px;
  }
}

.page-block{
  &__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title{
    font-size: 44px;
    font-weight: 700;
    color: #FFFFFF;
  }
  &__content{
    margin-top: 44px;
  }
}

.currency-select{
  background: none;
  outline: none;
  border: none;

  font-size: 34px;
  font-weight: 500;
  color:#fff;
}

.trigger{
  &.active{
    .trigger-block{
      display: block;
      z-index: 9;
    }
  }
  &-block{
    display: none;
  }
  &-link{
    &:hover{
      cursor: pointer;
    }
  }
}

.language-select{
  background: none;
  outline: none;
  border: none;

  text-transform: uppercase;
  color:#fff;
  font-weight: 500;

  letter-spacing: -0.3px;

  font-size: 14px;
}

.radio{
  &-input{
    &__label{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        cursor: pointer;
      }
    }
    &__item{
      display: none;
      &:checked ~ .radio-input__checkmark{
        background-color: #66FF66;
        &::before{
          transform: translateX(24px);
        }
      }
    }
    &__text{
      font-size: 18px;
      font-weight: 500;
      letter-spacing: -0.3px;

      color: #FFFFFF;
    }
    &__checkmark{
      position: relative;

      width: 60px;
      height: 36px;

      display: flex;
      align-items: center;
      justify-content: flex-start;

      background: #297BC7;

      border-radius: 30px;

      padding: 2px;

      transition: .3s all;
      &::before{
        position: absolute;
        content: '';
        width: 32px;
        height: 32px;
        display: block;
        background-color: #Fff;
        box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15), 0px 3px 1px rgba(0, 0, 0, 0.06);

        border-radius: 50%;

        transition: 0.3s all linear;

        left: 2px;
        top: 2px;
      }
    }
  }
}

.back-arrow{
  display: inline-flex;
  transform: rotate(180deg);

  margin-right: 20px;
}

.sort{
  &-item{
    display: flex;
    &__icon{
      width: 18px;
      height: 18px;
      svg{
        width: 100%;
        height: 100%;
      }
    }
    &__label + &__icon{
      margin-left: 12px;
    }
    &__icon + &__label{
      margin-left: 12px;
    }
    &__label{
      letter-spacing: -0.3px;

      color: #329DFF;

      font-size: 18px;

      display: block;
    }
    &__select{
      font-size: 18px;
      letter-spacing: -0.3px;
      color: #329DFF;

      background: none;
      outline: none;
      border:none;
    }
  }
}

.checkbox{
  &__label{
    display: flex;
    align-items: center;
  }
  &__input{
    display: none;
    &:checked ~ .checkbox__checkmark{
      border:none;
      &::before{
        opacity: 1;
      }
    }
    &:checked ~ .checkbox__content{
      color:#fff;
    }
    &:checked ~ .offer-info__checkbox-content{
      color:#297BC7;
    }
  }
  &__checkmark{
    width: 20px;
    height: 20px;

    display: block;

    border:2px solid #297BC7;

    position: relative;

    transition: .3s border-color;
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;
      display: block;

      background: url("../img/checkbox.svg") center center no-repeat;

      opacity: 0;

      transition: .3s opacity;
    }
  }
  &__content{
    margin-left: 15px;
    color:rgba(30, 91, 164, 1);
  }
}

@media screen and (max-width: 991px) {
  .page-content{
    width: 100%;
  }
}

@media screen and (max-width: 475px) {
  .page-content__wrapper{
    padding: 48px 15px;
  }
}