.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(0, 0, 0, 0.5);

  opacity: 0;
  z-index: -1;

  transition: .5s all;

  &.active{
    opacity: 1;
    z-index: 99;
  }

  &-close,&-trigger{
    &:hover{
      cursor: pointer;
    }
  }

  &-paid{
    &__title{
      letter-spacing: -0.3px;

      color: #FFFFFF;
      font-size: 22px;
      font-weight: 500;
    }
    &__desc{
      margin-top: 24px;

      letter-spacing: -0.3px;

      color: #FFFFFF;
      font-size: 18px;
      line-height: 24px;
    }
    &__footer{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 30px;

      margin-top: 60px;
      .btn{
        height: 60px;
      }
    }
  }

  &-requisites{
    &__text{
      letter-spacing: -0.3px;

      color: #329DFF;

      font-size: 18px;
      line-height: 24px;
    }
    &__text + &__text{
      margin-top: 16px;
    }
  }

  &__content{
    margin-top: 60px;
  }

  &__wrapper{
    max-width: 600px;
    width: 100%;

    background: #14386E;
    box-shadow: 0px 4px 90px rgba(0, 0, 0, 0.15);
    border-radius: 14px;

    padding: 35px;
  }

  &__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title{
    font-size: 30px;
    font-weight: 700;
    color:#fff;

    display: flex;
    align-items: center;
  }

  &__close{
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn{
    max-width: 250px;
    margin:60px auto 0 auto;
  }

  &__desc{
    font-size: 22px;
    font-weight: 500;
    letter-spacing: -0.3px;

    color: #FFFFFF;

  }

  &-notifications{
    &__content{
      max-height: 500px;
      overflow-y: auto;

      padding-right: 20px;
      padding-bottom: 35px;
    }

    &__wrapper{
      padding-bottom: 0;
    }
  }
  &-qr{
    &__address{
      margin-top: 25px;
      &-title{
        margin-bottom: 20px;
        font-size: 16px;
        color:#fff;
      }
    }
    &__wallet-address{
      margin-left: 0;
    }
    &__title{
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      color: #FFFFFF;
      font-size: 16px;
    }
    &__img{
      width: 100%;
      height: 430px;
      background-color: #fff;
      border-radius: 14px;
      display: flex;
      align-items: center;
      justify-content: center;

      margin-top: 20px;
      img{
        width: 285px;
        height: 285px;
        object-fit: contain;
      }
    }
  }
  &-close{
    &__back{
      display: block;
      transform: rotate(180deg);
      margin-right: 20px;
    }
  }
  &-graph{
    &__wrapper{
      max-width: 1010px;
    }
    &__nav{
      margin: 0 auto;
    }
    &__content{
      margin-top: 45px;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      gap: 20px;

      margin-top: 50px;
      &-title{
        font-size: 16px;
        color:#297BC7;
        letter-spacing: -0.03px;
      }
      &-text{
        font-size: 22px;
        font-weight: 500;
        color:#fff;
        letter-spacing: -0.03px;
        margin-top: 5px;
      }
    }
    &__title{
      display: flex;
      align-items: center;
      justify-content: center;
      &-img{
        width: 38px;
        height: 38px;
        display: block;
        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      &-label{
        font-size: 34px;
        font-weight: 500;
        color:#fff;
        margin: 0 15px;
      }
    }
  }
}


.currency-list{
  &__item{
    background: #002D58;
    border-radius: 14px;

    padding: 15px;
  }

  &__item + &__item{
    margin-top: 18px;
  }

  &__radio{
    margin-left: auto;
  }

  &__link{
    display: flex;
    align-items: center;
    &::after{
      content: '';
      width: 15px;
      height: 15px;
      display: block;

      margin-left: auto;

      background: url("../img/white-arrow.svg") center center no-repeat;
      background-size: contain;
    }
    &_payment{
      &::after{
        display: none;
      }
    }
    &-icon{
      height: 40px;
      width: 40px;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &-label{
      margin-left: 15px;
      font-size: 22px;

      color:#fff;
      span{
        font-weight: 700;
      }
    }
  }

}

.chart{
  height: 235px;
}


@media screen and (max-width: 475px) {
  .popup{
    &__wrapper{
      height: 100vh;
      border-radius: 0;
      overflow-y: auto;

      padding: 15px;
    }
    &-graph{
      &__grid{
        grid-template-columns: repeat(2,minmax(0,1fr));
        gap: 30px;
      }
      &__title{
        flex-direction: column;
      }
      &__nav{
        .tabs-nav__link{
          font-size: 14px;
        }
      }
    }
  }
}